import '@devsnc/sn-record-control-wrapper';
import { isEmpty } from 'lodash';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import '@servicenow/now-modal';
import '@servicenow/now-loader';
import { t } from 'sn-translate';
import '@servicenow/now-text-link';
import { UPLOAD_ATTACHMENT_BUTTON_CLICKED, VALUE_CHANGED } from './constants';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import 'sn-component-file-icon';
import {
	isImageType,
	getExtensionFromFileName,
	downloadAttachment
} from './attachmentUtils';
import renameContent from './renameModal';
import { isAttrTrue } from '@devsnc/sn-controls-common';

const FileThumbnail = ({ contentType, value, displayValue }) => {
	return isImageType(contentType) ? (
		<img
			className="sn-attachment-img"
			src={'/' + value + '.iix?t=small'}
			alt={displayValue}
		/>
	) : (
		<div className="sn-file-icon">
			<sn-file-icon
				className="sn-icon"
				size="md"
				mime-type={contentType}
				extension={getExtensionFromFileName(displayValue)}
			/>
		</div>
	);
};

export const EmptyState = ({ dispatch }) => {
	return (
		<Fragment>
			<now-button-bare
				icon-start="paperclip-outline"
				label="Attach File"
				size="md"
				variant="secondary"
			/>

			<input
				id="fileInput"
				className="now-a11y-label"
				type="file"
				aria-hidden="true"
				on-change={({ target: { files } }) =>
					dispatch(UPLOAD_ATTACHMENT_BUTTON_CLICKED, files[0])
				}
			/>
		</Fragment>
	);
};

export const EmptyStateReadOnly = () => (
	<div className="empty-state-readonly">
		<span>{t('No File Attached')}</span>
	</div>
);

export const FailedUploadState = ({
	dispatch,
	displayValue,
	fileContent,
	deleting
}) => (
	<div className="disabled-file">
		<div className="retry-content">
			<div className="retry-attachment-icon">
				<now-icon icon="paperclip-outline" size="md" />
			</div>
			<span className="retry-attachment-text">{displayValue}</span>
		</div>
		<div className="retryfile-buttons">
			<now-button-iconic
				bare
				icon="sync-outline"
				size="sm"
				variant="tertiary"
				on-click={() => {
					dispatch(UPLOAD_ATTACHMENT_BUTTON_CLICKED, fileContent);
				}}
			/>
			<now-button-iconic
				bare
				icon="trash-outline"
				size="sm"
				variant="tertiary"
				append-to-payload={{ button: 'delete' }}
			/>
			<now-modal
				opened={deleting}
				header-label={t('Delete Attachment')}
				content={t(
					'Are you sure you want to delete this attachment? Changes cannot be undone.'
				)}
				size="sm"
				footer-actions={[
					{
						label: 'Delete',
						variant: 'primary-negative'
					},
					{ label: 'Cancel', variant: 'secondary' }
				]}
			/>
		</div>
	</div>
);

export const VirusState = ({ displayValue, deleting }) => (
	<div className="disabled-file">
		<div className="infected-content">
			<div className="infected-attachment-icon">
				<now-icon icon="paperclip-outline" size="md" />
			</div>
			<span>{displayValue}</span>
		</div>
		<div className="infected-buttons">
			<now-button-iconic
				bare
				icon="trash-outline"
				size="sm"
				variant="tertiary"
				append-to-payload={{ button: 'delete' }}
			/>
			<now-modal
				opened={deleting}
				header-label={t('Delete Attachment')}
				content={t(
					'Are you sure you want to delete this attachment? Changes cannot be undone.'
				)}
				size="sm"
				footer-actions={[
					{
						label: 'Delete',
						variant: 'primary-negative'
					},
					{ label: 'Cancel', variant: 'secondary' }
				]}
			/>
		</div>
	</div>
);

export const DownloadAttachmentButton = ({ sysId }) => (
	<now-button-iconic
		bare
		icon="download-outline"
		size="sm"
		variant="tertiary"
		onclick={() => {
			downloadAttachment(sysId);
		}}
	/>
);

export const dispatchAttachmentPreview = ({
	dispatch,
	displayValue,
	value,
	contentType,
	readonly
}) => e => {
	e.preventDefault();

	dispatch('ATTACHMENT_PREVIEW#LOAD', {
		item: {
			name: displayValue,
			sysId: value,
			type: contentType,
			canDelete: true,
			readonly: isAttrTrue(readonly)
		}
	});
};

export const SuccessfulUploadState = ({
	dispatch,
	contentType,
	displayValue,
	readonly,
	value,
	editing,
	deleting,
	newFileName,
	edited
}) => (
	<div className="container">
		<div className="attachments-content">
			<FileThumbnail
				contentType={contentType}
				value={value}
				displayValue={displayValue}
			/>
			<now-text-link
				label={displayValue}
				variant="primary"
				on-click={dispatchAttachmentPreview({
					dispatch,
					displayValue,
					value,
					contentType,
					readonly
				})}
			/>
		</div>
		{readonly !== 'true' ? (
			<div className="buttons-content">
				<DownloadAttachmentButton sysId={value} />
				<now-button-iconic
					bare
					icon="pencil-page-outline"
					size="sm"
					variant="tertiary"
					append-to-payload={{ button: 'edit' }}
				/>
				<now-button-iconic
					bare
					icon="trash-outline"
					size="sm"
					variant="tertiary"
					append-to-payload={{ button: 'delete' }}
				/>
				<now-modal
					opened={editing}
					header-label={t('Edit Attachment Name')}
					content={renameContent(dispatch, newFileName)}
					footer-actions={[
						{
							label: t('Submit'),
							variant: 'primary',
							...(isAttrTrue(edited) && !isEmpty(newFileName)
								? {}
								: { disabled: true })
						},
						{ label: t('Cancel'), variant: 'secondary' }
					]}
				/>
				<now-modal
					opened={deleting}
					header-label={t('Delete Attachment')}
					content={t(
						'Are you sure you want to delete this attachment? Changes cannot be undone.'
					)}
					size="sm"
					footer-actions={[
						{
							label: 'Delete',
							variant: 'primary-negative'
						},
						{ label: 'Cancel', variant: 'secondary' }
					]}
				/>
			</div>
		) : (
			<div className="download-button">
				<DownloadAttachmentButton sysId={value} />
			</div>
		)}
	</div>
);

export const AttachmentLoadingState = () => (
	<div className="attachment-loading-container">
		<div className="attachment-loader">
			<now-loader size="md" />
		</div>
		<div className="attachment-loading-content">
			<span>{t('Uploading File...')}</span>
		</div>
	</div>
);
export default (state, { dispatch }) => {
	const {
		displayValue,
		name,
		onValueChange,
		value,
		invalid,
		fileContent,
		readonly
	} = state.properties;
	const {
		contentType,
		editing,
		deleting,
		newFileName,
		edited,
		attachmentLoading,
		virusState
	} = state;

	if (virusState === 'not_available' && !invalid) {
		let errorMessage = t('This file did not pass the virus scan.');
		if (onValueChange) {
			onValueChange(null, name, value, displayValue, {
				type: 'error',
				message: errorMessage
			});
		}
		dispatch(VALUE_CHANGED, {
			file_name: displayValue,
			sys_id: value,
			error: {
				type: 'error',
				message: errorMessage
			}
		});
		return;
	}

	const isInitialState = isEmpty(displayValue) && !isAttrTrue(readonly);
	const isInitialStateAndReadOnly = isEmpty(displayValue);
	const attachmentLoadingState =
		isEmpty(displayValue) && isAttrTrue(attachmentLoading);
	const uploadFailed = isEmpty(value) && !isEmpty(displayValue);
	const noVirus = virusState !== 'not_available';

	return (
		<sn-record-control-wrapper {...state.properties}>
			<div className="attachment-content">
				{(() => {
					switch (true) {
						case attachmentLoadingState:
							return <AttachmentLoadingState />;
						case isInitialState:
							return <EmptyState dispatch={dispatch} />;
						case isInitialStateAndReadOnly:
							return <EmptyStateReadOnly />;
						case uploadFailed:
							return (
								<FailedUploadState
									dispatch={dispatch}
									displayValue={displayValue}
									fileContent={fileContent}
									deleting={deleting}
								/>
							);
						case noVirus:
							return (
								<div className="file-container">
									<SuccessfulUploadState
										dispatch={dispatch}
										contentType={contentType}
										displayValue={displayValue}
										readonly={readonly}
										value={value}
										editing={editing}
										deleting={deleting}
										dispatch={dispatch}
										newFileName={newFileName}
										edited={edited}
									/>
								</div>
							);

						default:
							return (
								<div className="file-container">
									<VirusState displayValue={displayValue} deleting={deleting} />
								</div>
							);
					}
				})()}
			</div>
		</sn-record-control-wrapper>
	);
};
