import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './fileattachment.scss';
import attachmentActions from './actions';
import view from './view';

const COMPONENT_NAME = 'sn-record-file-attachment';

/**
 *
 *  Simple File Attachment Component
 *```jsx
 *	<sn-record-file-attachment
 *		label="I am File attachment"
 *		value={state.value}
 *		displayValue={state.displayValue}
 *	/>
 *
 *```
 * @seismicElement sn-record-file-attachment
 * @summary *  File Attachment Component.
 * */

createCustomElement(COMPONENT_NAME, {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		contentType: '',
		editing: false,
		newFileName: '',
		edited: false,
		attachmentLoading: false,
		virusState: ''
	},
	properties: {
		/**
		 * Additional content describing the field, usually to provide further
		 * instruction to the user.
		 * @type {string}
		 */
		description: {},
		/**
		 * Additional messages used to give status information on a field, usually
		 * to provide error messages or warnings
		 * @type {array}
		 */
		messages: { default: [] },
		/**
		 * Flag indicating whether there is an error
		 * @type {string | boolean }
		 */
		error: { default: '' },
		/**
		 * Specifies whether the current value is not valid.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true. Used by the glide form controls demo
		 * @type {string | boolean}
		 * @private
		 */
		invalid: {},
		/**
		 * It's a label! All form controls need a label!
		 * @type {string}
		 */
		label: {},
		/**
		 * The language for the character used in the input field
		 * @type {string}
		 */
		language: {},
		/**
		 * Name for the component. Any events dispatched by the component will use this name.
		 * @type {string}
		 */
		name: {},
		/**
		 * Callbacks for Forms consumption
		 * @type {function}
		 * @private
		 */
		onValueChange: {},
		/**
		 * Specifies that the user cannot modify the value of the component.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 */
		readonly: { default: false },
		/**
		 * Whether or not the user must fill in a value for this component.
		 * Can be specified as a boolean or the string "true"
		 * @type {string | boolean}
		 */
		required: { default: false },
		/**
		 * Specifies that the field should be rendered right-to-left.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 * @private
		 */
		reversed: { default: false },
		/**
		 * Name of file name of the attachment
		 * @type {string}
		 */
		displayValue: { default: '' },
		/**
		 * SysId of table to which file gets attached
		 * @type {string}
		 */
		tableSysId: { default: '' },
		/**
		 * Name of table where file gets attached to.
		 * @type {string}
		 */
		tableName: { default: '' },
		/**
		 * SysId of attachment record in sys_attachment table
		 * @type {string}
		 */
		value: { default: '' },
		/**
		 * Content type of file attached
		 * @type {string}
		 */
		contentType: { default: '' },
		/**
		 * Virus scan state of attachment
		 * @type {string}
		 */
		virusState: { default: '' },
		/**
		 * List of file extensions that can be attached
		 * @type {string}
		 */
		extensions: { default: '' },
		/**
		 * File Content of file attachment
		 * @type {object}
		 */
		fileContent: {},
		/**
		 * Maximum size limit an attachment can support
		 * @type {string}
		 */
		maxAttachmentSize: { default: '' }
	},
	...attachmentActions,

	dispatches: {
		/**
		 * Dispatches when attachment records gets created in sys_attachment table.
		 * @type {{ value: string,displayValue: string, error: { type: string, message: string } }}
		 */
		'SN_RECORD_FILE_ATTACHMENT#VALUE_CHANGED': {}
	},
	styles
});

export default COMPONENT_NAME;
