export const getExtensionFromFileName = name => {
	if (name.lastIndexOf('.') === -1) return '';

	return name.substring(name.lastIndexOf('.') + 1).toLowerCase();
};

export const isInvalidFileType = (fileName, modifiedExtensions) => {
	if (modifiedExtensions.length < 1) return false;
	return !modifiedExtensions.includes(getExtensionFromFileName(fileName));
};

export const isImageType = contentType => {
	return ['image'].includes(contentType.substring(0, contentType.indexOf('/')));
};

export const downloadAttachment = sysId => {
	window.location.href = `/sys_attachment.do?sys_id=${sysId}`;
};

export const getFileNameOnly = fileName => {
	if (fileName) {
		const index = fileName.lastIndexOf('.');
		return index > 0 ? fileName.substring(0, index) : fileName;
	}
	return fileName;
};
