import { createHttpEffect } from '@servicenow/ui-effect-http';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { t } from 'sn-translate';
import {
	isInvalidFileType,
	getFileNameOnly,
	getExtensionFromFileName
} from './attachmentUtils';
import { compact, get, toNumber } from 'lodash';
import {
	VALUE_CHANGED,
	POST_DATA_SUCCESS,
	POST_DATA_FAILURE,
	MODAL_OPEN_CHANGED,
	UPDATE_FILE_NAME,
	RENAME_SUCCESS,
	ATTACHMENT_FILE_UPLOAD_BUTTON_CLICKED
} from './constants';
import { FileAttachmentGraphQLUpdate as update } from './datasources/FileAttachmentGraphQLUpdate';

export const uploadAttachmentEffect = ({
	action,
	dispatch,
	state,
	updateProperties,
	updateState
}) => {
	const {
		tableName,
		tableSysId,
		extensions,
		onValueChange,
		name,
		messages,
		invalid,
		maxAttachmentSize
	} = state.properties;
	const selectedFile = action.payload;
	const { name: file_name } = selectedFile;

	if (!invalid) {
		updateProperties({ fileContent: selectedFile });
	}

	const modifiedExtensions = compact((extensions || '').split(','));
	if (isInvalidFileType(file_name, modifiedExtensions)) {
		let errorMessage = t(
			'Selected file type is invalid. Valid field types: {0}',
			extensions
		);
		if (onValueChange) {
			onValueChange(null, name, '', '', {
				type: 'error',
				message: errorMessage
			});
		} else {
			dispatch(VALUE_CHANGED, {
				error: {
					type: 'error',
					message: errorMessage
				}
			});
		}
		return;
	}

	if (
		maxAttachmentSize &&
		selectedFile.size / 1000000 > toNumber(maxAttachmentSize)
	) {
		let errorMessage = t(
			'The selected file is too large. Maximum upload file size: {0}MB.',
			maxAttachmentSize
		);
		if (onValueChange) {
			onValueChange(null, name, '', '', {
				type: 'error',
				message: errorMessage
			});
		} else {
			dispatch(VALUE_CHANGED, {
				error: {
					type: 'error',
					message: errorMessage
				}
			});
		}
		return;
	}

	updateState({
		attachmentLoading: true
	});

	const errorMessage = t('File upload in progress');
	if (onValueChange) {
		onValueChange(null, name, '', '', {
			type: 'error',
			message: errorMessage
		});
	} else {
		dispatch(VALUE_CHANGED, {
			error: {
				type: 'error',
				message: errorMessage
			}
		});
	}

	const modifiedTableName = 'ZZ_YY' + tableName;

	const data = new FormData();
	data.append('table_name', modifiedTableName);
	data.append('table_sys_id', tableSysId);
	data.append('file', selectedFile);
	dispatch('POST_ATTACHMENT_QUERY', {
		data
	});
};

export const postAttachmentEffect = createHttpEffect(
	'/api/now/attachment/upload',
	{
		method: 'POST',
		batch: false,
		headers: { 'Content-Type': 'multipart/form-data' },
		dataParam: 'data',
		successActionType: POST_DATA_SUCCESS,
		errorActionType: POST_DATA_FAILURE
	}
);

export const postAttachmentSuccessEffect = ({
	action,
	dispatch,
	state,
	updateState
}) => {
	const {
		result: { file_name, sys_id, content_type }
	} = action.payload;
	const { name, onValueChange } = state.properties;

	updateState({
		contentType: content_type,
		newFileName: getFileNameOnly(file_name),
		attachmentLoading: false
	});

	if (onValueChange) {
		onValueChange(null, name, sys_id, file_name, null);
	} else {
		dispatch(VALUE_CHANGED, {
			file_name,
			sys_id
		});
	}
};

export const postAttachmentFailureEffect = ({
	action,
	dispatch,
	state,
	updateState
}) => {
	const { status } = action.payload;
	const { name, onValueChange, maxAttachmentSize } = state.properties;
	const file_name = action.meta.request.data.get('file').name;

	updateState({
		attachmentLoading: false
	});

	let errorMessage = t('Unable to upload file. Please try again');
	if (onValueChange) {
		onValueChange(null, name, '', file_name, {
			type: 'error',
			message: errorMessage
		});
	} else {
		dispatch(VALUE_CHANGED, {
			file_name,
			error: {
				type: 'error',
				message: errorMessage
			}
		});
	}
};

export const componentBootstrappedEffect = ({ state, updateState }) => {
	const { contentType, displayValue, virusState } = state.properties;
	updateState({
		contentType,
		virusState,
		newFileName: getFileNameOnly(displayValue)
	});
};

export const buttonClickedEffect = ({ updateState, action }) => {
	const { button } = action.payload;
	if (button === 'edit')
		updateState({
			editing: true,
			edited: false
		});
	if (button === 'delete')
		updateState({
			deleting: true
		});
};

export const modalButtonClickedEffect = ({ action, dispatch, state }) => {
	const { variant } = action.payload.action;
	const { newFileName } = state;
	const { name, displayValue, value, onValueChange } = state.properties;
	const changed =
		newFileName != '' && newFileName != getFileNameOnly(displayValue);

	if (variant === 'primary' && changed) {
		const extension = getExtensionFromFileName(displayValue);
		const composedFileName = newFileName + '.' + extension;

		dispatch(UPDATE_FILE_NAME, {
			file_name: composedFileName,
			sys_id: value
		});
	}

	if (variant === 'primary-negative') {
		if (onValueChange) {
			onValueChange(null, name, '', '', null);
		}
		dispatch(VALUE_CHANGED, {
			file_name: '',
			sys_id: ''
		});
	}

	dispatch(MODAL_OPEN_CHANGED, { value: false });
};

export const renameSuccessEffect = ({
	action,
	state,
	dispatch,
	updateState
}) => {
	const fileName = get(
		action,
		'payload.data.GlideRecord_Mutation.update_sys_attachment.file_name.value',
		''
	);
	const { onValueChange, value, name } = state.properties;

	updateState({
		newFileName: getFileNameOnly(fileName)
	});

	if (onValueChange) {
		onValueChange(null, name, value, fileName, null);
	}
	dispatch(VALUE_CHANGED, {
		file_name: fileName,
		sys_id: value
	});
};

export const renameFailureEffect = ({ dispatch, state }) => {
	const { onValueChange, value, name, displayValue } = state.properties;
	let errorMessage = t('Unable to rename file. Please try again');
	if (onValueChange) {
		onValueChange(null, name, value, displayValue, {
			type: 'error',
			message: errorMessage
		});
	}
	dispatch(VALUE_CHANGED, {
		file_name: displayValue,
		error: {
			type: 'error',
			message: errorMessage
		}
	});
};
export const updateAttachment = createGraphQLEffect(update, {
	variableList: ['sys_id', 'file_name'],
	successActionType: RENAME_SUCCESS,
	errorActionType: POST_DATA_FAILURE
});
export const modalOpenedChangeEffect = ({ action, updateState, state }) => {
	const { value } = action.payload;
	const { displayValue } = state.properties;

	updateState({
		editing: value,
		deleting: value,
		newFileName: getFileNameOnly(displayValue),
		virusState: ''
	});
};
export const modalInputChangeEffect = ({ action, state, updateState }) => {
	const newFileName = action.payload.newFileName;
	const { displayValue } = state.properties;
	const { edited } = state;
	const changed = newFileName != getFileNameOnly(displayValue);

	//After first edit, use newFileName as value for input
	if (changed || edited) {
		updateState({
			edited: true,
			newFileName
		});
	}
};

export const nowButtonBareEffect = ({ dispatch }) => {
	dispatch(ATTACHMENT_FILE_UPLOAD_BUTTON_CLICKED);
};

export const attachmentFileUploadButtonEffect = coeffects => {
	const { host } = coeffects;
	const inputNode = host.shadowRoot.querySelector('input');
	inputNode.click();
};
