import '@devsnc/sn-record-input';
import { t } from 'sn-translate';
import { FILE_RENAME_INPUT } from './constants';

export default (dispatch, newFileName) => (
	<sn-record-input
		value={newFileName}
		label={t('Name')}
		required
		maxlength="40"
		onValueChange={({ value }) => {
			dispatch(FILE_RENAME_INPUT, { newFileName: value });
		}}
	/>
);
